<template>
  <div class="settings">
    <div class="row">
      <div class="flex xs12 sm12 md12 lg12">
        <Transfers :loading="loading" :value="transfers" />
      </div>
    </div>
    <div class="row">
      <div class="flex xs12 sm12 md12 lg6">
        <DepositsRequests :loading="loading" :value="requests" />
      </div>
      <div class="flex xs12 sm12 md12 lg6">
        <DepositsVerified :loading="loading" :value="verified" />
      </div>
    </div>
  </div>
</template>

<script>
import DepositsRequests from './DepositsRequests'
import DepositsVerified from './DepositsVerified'
import Transfers from './Transfers'
import * as Transactions from '@/api/Transactions'

export default {
  name: 'Deposits',
  components: { DepositsRequests, DepositsVerified, Transfers },
  data: () => ({
    loading: true,
    deposits: [],
    transfers: [],
    timeouts: {
      updateDeposits: null,
      updateTransfers: null,
    },
  }),
  computed: {
    storedDeposits() {
      return this.$store.getters.deposits
    },
    storedTransfers() {
      return this.$store.getters.transfers
    },
    requests() {
      return this.deposits.filter(
        d => d.status == 'processing' || d.status == 'pending'
      )
    },
    verified() {
      return this.deposits.filter(
        d => (d.status == 'completed' || d.status == 'success') && !d.fake
      )
    },
  },
  methods: {
    updateDeposits() {
      Transactions.getDepositsList()
        .then(({ data: deposits }) => {
          this.loading = false
          this.deposits = deposits
          this.$store.commit('SET_DEPOSITS', deposits)
          this.timeouts.updateDeposits = setTimeout(this.updateDeposits, 3000)
        })
        .catch(() => {
          this.loading = false
          this.timeouts.updateDeposits = setTimeout(this.updateDeposits, 30000)
        })
    },
    updateTransfers() {
      Transactions.getTransfersList()
        .then(({ data: { transfers } }) => {
          this.loading = false
          this.transfers = transfers
          this.$store.commit('SET_TRANSFERS', transfers)
          this.timeouts.updateTransfers = setTimeout(this.updateTransfers, 3000)
        })
        .catch(() => {
          this.loading = false
          this.timeouts.updateTransfers = setTimeout(
            this.updateTransfers,
            30000
          )
        })
    },
  },
  mounted() {
    if (this.storedDeposits.length) {
      this.deposits = this.storedDeposits
      this.loading = false
      this.timeouts.updateDeposits = setTimeout(this.updateDeposits, 30000)
    } else {
      this.updateDeposits()
    }

    if (this.storedTransfers.length) {
      this.transfers = this.storedTransfers
      this.loading = false
      this.timeouts.updateTransfers = setTimeout(this.updateTransfers, 30000)
    } else {
      this.updateTransfers()
    }
  },
  beforeDestroy() {
    Object.values(this.timeouts).forEach(t => {
      clearTimeout(t)
    })
  },
}
</script>

