<template>
  <va-card :color="computedWidgetColor" title="All Transfers">
    <va-inner-loading :loading="loading">
      <va-data-table
        :fields="fields"
        :data="data"
        rowClass="user-row"
        :style="{
          opacity: loading ? 0.5 : 1,
        }"
      >
        <template v-slot:icon>
          <va-icon :name="'fa fa-check'" color="secondary" />
        </template>
        <template v-slot:date="props">
          {{ props.rowData.date }}
        </template>
        <template v-slot:email="props">
          {{ props.rowData.email }}
        </template>
        <template v-slot:network="props">
          {{ props.rowData.currency }}
        </template>
        <template v-slot:amount="props">
          {{ props.rowData.amount }}
        </template>
        <template v-slot:explore="props">
          <va-button small outline color="info" @click="explore(props.rowData)">
            Explore
          </va-button>
        </template>
      </va-data-table>
    </va-inner-loading>
  </va-card>
</template>

<script>
import moment from 'moment'

export default {
  name: 'Transfers',
  data: () => ({}),
  props: {
    loading: Boolean,
    value: Array,
  },
  methods: {
    explore(d) {
      this.value.forEach(deposit => {
        if (deposit._id === d._id) {
          window.open(deposit.url, '_blank')
        }
      })
    },
    check(val) {
      console.log(val)
    }
  },
  computed: {
    isDesktop() {
      return this.$store.getters.isDesktop
    },
    computedWidgetColor() {
      return this.$themes.background
    },
    data() {
      return this.value.map(dep => {
        let locale = this.$time.UTCToLocale(dep.at)
        let formated = this.$time.format(locale)
        return { ...dep, email: dep.recipientEmail || dep.recipient, date: formated }
      })
    },
    fields() {
      let result = [
        {
          name: '__slot:icon',
          width: '30px',
          dataClass: 'text-center',
        },
      ]

      if (this.isDesktop) {
        result.push({
          name: 'date',
          title: this.$t('tables.headings.date'),
          width: '20%',
        })
      }

      result = [
        ...result,
        {
          name: 'currency',
          title: this.$t('tables.headings.network'),
          width: '10%',
        },
        {
          name: 'amount',
          title: this.$t('tables.headings.amount'),
          width: '10%',
        },
        {
          name: 'email',
          title: this.$t('tables.headings.user'),
          width: '35%',
        },
        {
          name: '__slot:explore',
          title: this.$t('tables.headings.toExplorer'),
          width: '15%',
        },
      ]

      return result
    },
  },
}
</script>

